      <template v-if="TableData.instoragList.auditor_status==1">
        <el-button type="primary" @click="shenHe" v-if="authority.b_pass==1"> </el-button>
        <el-button @click="abolishFun" v-if="authority.b_invalid==1"> </el-button>
      </template>
      <template v-if="TableData.instoragList.auditor_status==2">
