<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportInstorageSee",
  data() {
    return {
      authority: {},
      TableData: {
        instoragList: {},
        goodsList: []
      },
      chaid: false
    };
  },
  computed: {
    ...mapGetters(["factorydetrptInstorageSeeGet"])
  },
  methods: {
    ...mapActions(["factorydetrptIndexACT"]),
    goBack() {
      this.factorydetrptIndexACT({
        moduleName: "Index",
        params: this.factorydetrptInstorageSeeGet.params
      });
    },
    AUDIT_GET() {
      //
      this.$axMethod
        .unAuditorPass({
          instorage_id: this.factorydetrptInstorageSeeGet.instorage_id
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
            this.goBack();
          }
        });
    },
    shenHe() {
      //
      this.$axios({
        method: "post",
        url: this.$api.purchaseAuditorPassInStorage,
        data: {
          instorage_id: this.factorydetrptInstorageSeeGet.instorage_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    abolishFun() {
      //
      this.$axios({
        method: "post",
        url: this.$api.purchaseAuditorInvalidInStorage,
        data: {
          instorage_id: this.factorydetrptInstorageSeeGet.instorage_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    // 
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 6) {
            sums[index] = parseFloat(sums[index]);
            return;
          } else if (index === 7) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
            return;
          } else {
            sums[index] = "";
            return;
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    /**  */
    this.instorage_id = this.factorydetrptInstorageSeeGet.instorage_id;
    this.$axios({
      method: "get",
      url: this.$api.purchaseInfoInStorage,
      params: {
        instorage_id: this.instorage_id
      }
    }).then(res => {
      if (res.data.code == 200) {
        this.TableData.instoragList = res.data.data.instoragList;
        this.TableData.goodsList = res.data.data.goodsList;
      }
    });
  }
};
</script>