<template>
  <div class="seePage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack"></span>
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label=":" size="mini">
          {{params.company_name}}
        </el-form-item>
        <el-form-item label=":" size="mini">
          {{params.factory_accounts_type_name}}
        </el-form-item>
        <el-form-item label=":" size="mini">
          {{params.business_time}}
        </el-form-item>

        <el-form-item label=":" size="mini">
          {{params.department_name}}
        </el-form-item>
        <el-form-item label=":" size="mini">
          {{params.payment_sn}}
        </el-form-item>
        <el-form-item :label="params.factory_accounts_type_name == '' ? ':' : '():'" size="mini">
          <template v-if="params.factory_accounts_type_name == ''">
            {{params.logistics_name}}
          </template>
          <template v-else>
            {{params.supplier_name}}
          </template>
        </el-form-item>
        <el-form-item label=":" size="mini">
          {{params.auditor_status_name}}
        </el-form-item>
        <el-form-item label=":" size="mini">
          {{params.remark}}
        </el-form-item>
      </el-form>
    </div>
    <div class="paymanageTitle">
      <i></i> <span></span>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="addTableData" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" label="">
          <template slot-scope="scope">
            {{scope.$index+1}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receivables_mode_name" label="">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_name" label="">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_number" label="">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="common_price" label="" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="">
        </el-table-column>
      </el-table>
    </div>
    <div class="aboutList" v-if="is_disabled">
      <div class="paymanageTitle">
        <i></i> <span></span>
      </div>
      <div class="table_container">
        <div>
          <el-table :highlight-current-row="true" tooltip-effect="light" border :data="instorageTable" show-summary :summary-method="getSummariesAboutlist" max-height="600">
            <el-table-column :show-overflow-tooltip="true" label="">
              <template slot-scope="scope">
                {{scope.$index+1}}
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_sn" label=""></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="type_name" label=""></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="supplier_name" label="()"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="instorage_num" label="" align="right"></el-table-column>
            <el-table-column v-if="params.factory_accounts_type_id==10001" :show-overflow-tooltip="true" property="instorage_price" label="" align="right"></el-table-column>
            <el-table-column v-if="params.factory_accounts_type_id==10002" :show-overflow-tooltip="true" property="order_price" label="" align="right"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="business_time" label="" width="200"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="add_time" label="" width="200"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="price" label="" align="right"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="rest_price" label="" align="right"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_price" label="" align="right"></el-table-column>
          </el-table>
        </div>

      </div>
      <p style="margin-top: 10px;font-size: 13px;">
        
      </p>
    </div>
    <div slot="footer" class="dialog-footer">
      <!---->
      <template v-if="params.auditor_status==1">
        <el-button type="primary" @click="examineFun" v-if="params.auditor_status==1"> </el-button>
        <el-button @click="abolishFun"> </el-button>
      </template>
      <!--  -->
      <template v-if="params.auditor_status==2">
        <el-button plain @click="AUDIT_GET" v-if="authority.b_nopass==1"></el-button>
      </template>
      <template v-if="params.auditor_status==2 || params.auditor_status==3">
        <el-button @click="goBack"> </el-button>
      </template>
      <template v-else>
        <el-button @click="goBack"> </el-button>
      </template>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportPaymanageSee",
  data() {
    return {
      authority: {},
      addTableData: [],
      params: {},
      is_disabled: false,
      instorageTable: []
    };
  },
  computed: {
    ...mapGetters(["factorydetrptPaymanageSeeGet"])
  },
  methods: {
    ...mapActions(["factorydetrptIndexACT"]),
    goBack() {
      this.factorydetrptIndexACT({
        moduleName: "Index",
        params: this.factorydetrptPaymanageSeeGet.params
      });
    },
    examineFun(status) {
      //
      let payment = this.factorydetrptPaymanageSeeGet.payment_id;
      this.$axMethod
        .purchaseAuditorPass({
          payment_id: payment
        })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$message({
              message: "",
              type: "success"
            });
            this.selmethons();
          }
        });
    },
    abolishFun() {
      //
      let payment = this.factorydetrptPaymanageSeeGet.payment_id;
      this.$axMethod
        .purchaseAuditorInvalid({
          payment_id: payment
        })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$message({
              message: "",
              type: "success"
            });
            this.selmethons();
            this.goBack();
          }
        });
    },
    AUDIT_GET() {
      this.$axMethod
        .auditorNoPassF({
          payment_id: this.factorydetrptPaymanageSeeGet.payment_id
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
            this.goBack();
          }
        });
    },
    getSummariesAboutlist(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 4) {
            sums[index] = parseFloat(sums[index]);
          } else if ([8, 9, 10].includes(index)) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
    selmethons() {
      let obj = {
        payment_id: this.factorydetrptPaymanageSeeGet.payment_id
      };
      this.$axMethod.purchaseGetPaymentInfo(obj).then(({ data }) => {
        if (data.code == 200) {
          let datataple = data.data;
          this.$set(this, "params", datataple.paymentInfo);
          this.$set(this, "addTableData", datataple.paymentCommonList);
          this.$set(this, "instorageTable", datataple.list);
          if (datataple.list.length) {
            this.$set(this, "is_disabled", true);
          }
        }
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 4) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.selmethons();
  }
};
</script>

